import React from 'react';
import { StyleSheet } from 'react-native';
import { Layout, Select, SelectItem } from '@ui-kitten/components';

const useSelectChanges = (initialState = undefined) => {
  const [selectedIndex, setSelectedIndex] = React.useState(initialState);
  return { selectedIndex, onSelect: setSelectedIndex };
};

export const SelectSizeShowcase = () => {

  const smallSelectChanges = useSelectChanges();
  const mediumSelectChanges = useSelectChanges();
  const largeSelectChanges = useSelectChanges();

  return (
    <Layout>

      <Select
        style={styles.select}
        size='small'
        placeholder='Small'
        {...smallSelectChanges}>
        <SelectItem title='Option 1'/>
        <SelectItem title='Option 2'/>
        <SelectItem title='Option 3'/>
      </Select>

      <Select
        style={styles.select}
        size='medium'
        placeholder='Medium'
        {...mediumSelectChanges}>
        <SelectItem title='Option 1'/>
        <SelectItem title='Option 2'/>
        <SelectItem title='Option 3'/>
      </Select>

      <Select
        style={styles.select}
        size='large'
        placeholder='Large'
        {...largeSelectChanges}>
        <SelectItem title='Option 1'/>
        <SelectItem title='Option 2'/>
        <SelectItem title='Option 3'/>
      </Select>

    </Layout>
  );
};

const styles = StyleSheet.create({
  select: {
    margin: 8,
  },
});
