import React from 'react';
import { Image, StyleSheet } from 'react-native';
import { Button, Layout, MenuItem, OverflowMenu } from '@ui-kitten/components';

const RemoteStarIcon = (props) => (
  <Image {...props} source={{ uri: 'https://akveo.github.io/eva-icons/fill/png/128/star.png' }}/>
);

export const OverflowMenuExternalSourceIconsShowcase = () => {

  // Updating `selectedIndex` if marking items selected is not needed.
  const [selectedIndex, setSelectedIndex] = React.useState(null);
  const [menuVisible, setMenuVisible] = React.useState(false);

  const onItemSelect = (index) => {
    setSelectedIndex(index);
    setMenuVisible(false);
  };

  const toggleMenu = () => {
    setMenuVisible(!menuVisible);
  };

  const renderToggleButton = () => (
    <Button onPress={toggleMenu}>
      TOGGLE MENU
    </Button>
  );

  return (
    <Layout style={styles.container}>
      <OverflowMenu
        anchor={renderToggleButton}
        visible={menuVisible}
        selectedIndex={selectedIndex}
        onSelect={onItemSelect}
        onBackdropPress={toggleMenu}>
        <MenuItem accessoryLeft={RemoteStarIcon} title='Users'/>
        <MenuItem accessoryLeft={RemoteStarIcon} title='Orders'/>
        <MenuItem accessoryLeft={RemoteStarIcon} title='Transactions'/>
      </OverflowMenu>
    </Layout>
  );
};

const styles = StyleSheet.create({
  container: {
    minHeight: 256,
  },
});
