import React from 'react';
import { StyleSheet, View } from 'react-native';
import { Autocomplete, AutocompleteItem, Layout } from '@ui-kitten/components';

const movies = [
  { title: 'Star Wars' },
  { title: 'Back to the Future' },
  { title: 'The Matrix' },
  { title: 'Inception' },
  { title: 'Interstellar' },
];

const useAutocompleteState = (initialState) => {
  const [value, setValue] = React.useState(initialState.value);
  const [data, setData] = React.useState(initialState.data);

  const onSelect = (index) => {
    setValue(data[index].title);
  };

  const onChangeText = (query) => {
    setValue(query);
    setData(movies.filter(item => item.title.toLowerCase().includes(query.toLowerCase())));
  };

  return { value, data, onSelect, onChangeText };
};

export const AutocompleteStatusShowcase = () => {

  const primaryAutocompleteState = useAutocompleteState({ data: movies });
  const successAutocompleteState = useAutocompleteState({ data: movies });
  const infoAutocompleteState = useAutocompleteState({ data: movies });
  const warningAutocompleteState = useAutocompleteState({ data: movies });
  const dangerAutocompleteState = useAutocompleteState({ data: movies });
  const basicAutocompleteState = useAutocompleteState({ data: movies });
  const controlAutocompleteState = useAutocompleteState({ data: movies });

  const renderOption = (item, index) => (
    <AutocompleteItem
      key={index}
      title={item.title}
    />
  );

  return (
    <Layout>

      <Autocomplete
        style={styles.autocomplete}
        status='primary'
        placeholder='Primary'
        {...primaryAutocompleteState}>
        {primaryAutocompleteState.data.map(renderOption)}
      </Autocomplete>

      <Autocomplete
        style={styles.autocomplete}
        status='success'
        placeholder='Success'
        {...successAutocompleteState}>
        {successAutocompleteState.data.map(renderOption)}
      </Autocomplete>

      <Autocomplete
        style={styles.autocomplete}
        status='info'
        placeholder='Info'
        {...infoAutocompleteState}>
        {infoAutocompleteState.data.map(renderOption)}
      </Autocomplete>

      <Autocomplete
        style={styles.autocomplete}
        status='warning'
        placeholder='Warning'
        {...warningAutocompleteState}>
        {warningAutocompleteState.data.map(renderOption)}
      </Autocomplete>

      <Autocomplete
        style={styles.autocomplete}
        status='danger'
        placeholder='Danger'
        {...dangerAutocompleteState}>
        {dangerAutocompleteState.data.map(renderOption)}
      </Autocomplete>

      <Autocomplete
        style={styles.autocomplete}
        status='basic'
        placeholder='Basic'
        {...basicAutocompleteState}>
        {basicAutocompleteState.data.map(renderOption)}
      </Autocomplete>

      <View style={styles.controlContainer}>
        <Autocomplete
          style={styles.autocomplete}
          status='control'
          placeholder='Control'
          {...controlAutocompleteState}>
          {controlAutocompleteState.data.map(renderOption)}
        </Autocomplete>
      </View>

    </Layout>
  );
};

const styles = StyleSheet.create({
  autocomplete: {
    margin: 8,
  },
  controlContainer: {
    borderRadius: 4,
    margin: 8,
    backgroundColor: '#3366FF',
  },
});

