import React from 'react';
import { StyleSheet, View } from 'react-native';
import { Button, IndexPath, Layout, Select, SelectGroup, SelectItem, Tooltip } from '@ui-kitten/components';

const placements = {
  top: [
    'top',
    'top start',
    'top end',
  ],
  bottom: [
    'bottom',
    'bottom start',
    'bottom end',
  ],
  left: [
    'left',
    'left start',
    'left end',
  ],
  right: [
    'right',
    'right start',
    'right end',
  ],
};

const findPlacementForIndexPath = (index) => {
  const group = Object.keys(placements)[index.row];
  return placements[group][index.section];
};

export const TooltipPlacementShowcase = () => {

  const [visible, setVisible] = React.useState(false);
  const [placementIndex, setPlacementIndex] = React.useState(new IndexPath(1, 0));
  const placement = findPlacementForIndexPath(placementIndex);

  const onPlacementSelect = (index) => {
    setPlacementIndex(index);
  };

  const toggleTooltip = () => {
    setVisible(!visible);
  };

  const renderToggleButton = () => (
    <Button onPress={toggleTooltip}>
      TOGGLE TOOLTIP
    </Button>
  );

  const onPlacementItemPress = ({ index }) => {
    setPlacementIndex(index);
  };

  const renderPlacementItem = (title) => (
    <SelectItem title={title} onPress={onPlacementItemPress}/>
  );

  const renderPlacementGroup = (title) => (
    <SelectGroup title={title}>
      {placements[title].map(renderPlacementItem)}
    </SelectGroup>
  );

  return (
    <Layout>

      <Select
        placeholder='Select Placement'
        value={placement}
        selectedIndex={placementIndex}
        onSelect={onPlacementSelect}>
        {Object.keys(placements).map(renderPlacementGroup)}
      </Select>

      <View style={styles.buttonContainer}>
        <Tooltip
          anchor={renderToggleButton}
          visible={visible}
          placement={placement.text}
          onBackdropPress={toggleTooltip}>
          Hi!
        </Tooltip>
      </View>

    </Layout>
  );
};

const styles = StyleSheet.create({
  buttonContainer: {
    justifyContent: 'center',
    alignItems: 'center',
    height: 376,
  },
  button: {
    width: 192,
  },
});
