import React from 'react';
import { StyleSheet } from 'react-native';
import { Tab, TabView, Text } from '@ui-kitten/components';

export const TabViewLazyLoadingShowcase = () => {

  const [selectedIndex, setSelectedIndex] = React.useState(0);

  const shouldLoadComponent = (index) => index === selectedIndex;

  return (
    <TabView
      selectedIndex={selectedIndex}
      shouldLoadComponent={shouldLoadComponent}
      onSelect={setSelectedIndex}>
      <Tab style={styles.tabContainer} title='USERS'>
        <Text category='h5'>USERS</Text>
      </Tab>
      <Tab style={styles.tabContainer} title='ORDERS'>
        <Text category='h5'>ORDERS</Text>
      </Tab>
      <Tab style={styles.tabContainer} title='TRANSACTIONS'>
        <Text category='h5'>ORDERS</Text>
      </Tab>
    </TabView>
  );
};

const styles = StyleSheet.create({
  tabContainer: {
    height: 64,
    alignItems: 'center',
    justifyContent: 'center',
  },
});
