import React from 'react';
import { StyleSheet, View } from 'react-native';
import { Layout, Select, SelectItem } from '@ui-kitten/components';

const useSelectChanges = (initialState = undefined) => {
  const [selectedIndex, setSelectedIndex] = React.useState(initialState);
  return { selectedIndex, onSelect: setSelectedIndex };
};

export const SelectStatusShowcase = () => {

  const primarySelectChanges = useSelectChanges();
  const successSelectChanges = useSelectChanges();
  const infoSelectChanges = useSelectChanges();
  const warningSelectChanges = useSelectChanges();
  const dangerSelectChanges = useSelectChanges();
  const basicSelectChanges = useSelectChanges();
  const controlSelectChanges = useSelectChanges();

  return (
    <Layout>

      <Select
        style={styles.select}
        status='primary'
        placeholder='Primary'
        {...primarySelectChanges}>
        <SelectItem title='Option 1'/>
        <SelectItem title='Option 2'/>
        <SelectItem title='Option 3'/>
      </Select>

      <Layout style={styles.rowContainer}>

        <Select
          style={styles.select}
          status='success'
          placeholder='Success'
          {...successSelectChanges}>
          <SelectItem title='Option 1'/>
          <SelectItem title='Option 2'/>
          <SelectItem title='Option 3'/>
        </Select>

        <Select
          style={styles.select}
          status='info'
          placeholder='Info'
          {...infoSelectChanges}>
          <SelectItem title='Option 1'/>
          <SelectItem title='Option 2'/>
          <SelectItem title='Option 3'/>
        </Select>

      </Layout>

      <Layout style={styles.rowContainer}>

        <Select
          style={styles.select}
          status='warning'
          placeholder='Warning'
          {...warningSelectChanges}>
          <SelectItem title='Option 1'/>
          <SelectItem title='Option 2'/>
          <SelectItem title='Option 3'/>
        </Select>

        <Select
          style={styles.select}
          status='danger'
          placeholder='Danger'
          {...dangerSelectChanges}>
          <SelectItem title='Option 1'/>
          <SelectItem title='Option 2'/>
          <SelectItem title='Option 3'/>
        </Select>

      </Layout>

      <Layout style={styles.rowContainer}>

        <Select
          style={styles.select}
          status='basic'
          placeholder='Basic'
          {...basicSelectChanges}>
          <SelectItem title='Option 1'/>
          <SelectItem title='Option 2'/>
          <SelectItem title='Option 3'/>
        </Select>

        <View style={styles.controlContainer}>
          <Select
            style={styles.select}
            status='control'
            placeholder='Control'
            {...controlSelectChanges}>
            <SelectItem title='Option 1'/>
            <SelectItem title='Option 2'/>
            <SelectItem title='Option 3'/>
          </Select>
        </View>

      </Layout>

    </Layout>
  );
};

const styles = StyleSheet.create({
  select: {
    flex: 1,
    margin: 8,
  },
  rowContainer: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  controlContainer: {
    borderRadius: 4,
    margin: 8,
    backgroundColor: '#3366FF',
  },
});
