import React from 'react';
import { StyleSheet } from 'react-native';
import { Autocomplete, AutocompleteItem, Layout } from '@ui-kitten/components';

const movies = [
  { title: 'Star Wars' },
  { title: 'Back to the Future' },
  { title: 'The Matrix' },
  { title: 'Inception' },
  { title: 'Interstellar' },
];

const useAutocompleteState = (initialState) => {
  const [value, setValue] = React.useState(initialState.value);
  const [data, setData] = React.useState(initialState.data);

  const onSelect = (index) => {
    setValue(data[index].title);
  };

  const onChangeText = (query) => {
    setValue(query);
    setData(movies.filter(item => item.title.toLowerCase().includes(query.toLowerCase())));
  };

  return { value, data, onSelect, onChangeText };
};

export const AutocompleteSizeShowcase = () => {

  const smallAutocompleteState = useAutocompleteState({ data: movies });
  const mediumAutocompleteState = useAutocompleteState({ data: movies });
  const largeAutocompleteState = useAutocompleteState({ data: movies });

  const renderOption = (item, index) => (
    <AutocompleteItem
      key={index}
      title={item.title}
    />
  );

  return (
    <Layout>

      <Autocomplete
        style={styles.autocomplete}
        size='small'
        placeholder='Small'
        {...smallAutocompleteState}>
        {smallAutocompleteState.data.map(renderOption)}
      </Autocomplete>

      <Autocomplete
        style={styles.autocomplete}
        size='medium'
        placeholder='Medium'
        {...mediumAutocompleteState}>
        {mediumAutocompleteState.data.map(renderOption)}
      </Autocomplete>

      <Autocomplete
        style={styles.autocomplete}
        size='large'
        placeholder='Large'
        {...largeAutocompleteState}>
        {largeAutocompleteState.data.map(renderOption)}
      </Autocomplete>

    </Layout>
  );
};

const styles = StyleSheet.create({
  autocomplete: {
    margin: 8,
  },
});

