import React from 'react';
import { Image } from 'react-native';
import { Button, Tooltip } from '@ui-kitten/components';

const InfoIcon = (props) => (
  <Image {...props} source={{ uri: 'https://akveo.github.io/eva-icons/fill/png/128/info.png' }}/>
);

export const TooltipWithExternalSourceIconShowcase = () => {

  const [visible, setVisible] = React.useState(false);

  const toggleTooltip = () => {
    setVisible(!visible);
  };

  const renderToggleButton = () => (
    <Button onPress={toggleTooltip}>
      TOGGLE TOOLTIP
    </Button>
  );

  return (
    <Tooltip
      anchor={renderToggleButton}
      visible={visible}
      accessoryLeft={InfoIcon}
      onBackdropPress={toggleTooltip}>
      Hi!
    </Tooltip>
  );
};
