import React from 'react';
import { StyleSheet } from 'react-native';
import { Icon, Layout, Select, SelectItem } from '@ui-kitten/components';

const StarIcon = (props) => (
  <Icon {...props} name='star'/>
);

export const SelectAccessoriesShowcase = () => {

  const [selectedIndex, setSelectedIndex] = React.useState();

  const onSelect = (index) => {
    setSelectedIndex(index);
  };

  return (
    <Layout style={styles.container}>
      <Select
        label='Label'
        caption='Caption'
        selectedIndex={selectedIndex}
        onSelect={onSelect}>
        <SelectItem accessoryLeft={StarIcon} title='Option 1'/>
        <SelectItem accessoryLeft={StarIcon} title='Option 2'/>
        <SelectItem accessoryLeft={StarIcon} title='Option 3'/>
      </Select>
    </Layout>
  );
};

const styles = StyleSheet.create({
  container: {
    minHeight: 228,
  },
});

