import React from 'react';
import { StyleSheet } from 'react-native';
import { Datepicker, Layout } from '@ui-kitten/components';

const useDatepickerChanges = (initialDate = null) => {
  const [date, setDate] = React.useState(initialDate);
  return { date, onSelect: setDate };
};

export const DatepickerStatusShowcase = () => {

  const primaryDatepickerChanges = useDatepickerChanges();
  const successDatepickerChanges = useDatepickerChanges();
  const infoDatepickerChanges = useDatepickerChanges();
  const warningDatepickerChanges = useDatepickerChanges();
  const dangerDatepickerChanges = useDatepickerChanges();
  const basicDatepickerChanges = useDatepickerChanges();

  return (
    <Layout style={styles.container}>

      <Layout style={styles.rowContainer}>

        <Datepicker
          style={styles.picker}
          status='primary'
          placeholder='Primary'
          {...primaryDatepickerChanges}
        />

        <Datepicker
          style={styles.picker}
          status='success'
          placeholder='Success'
          {...successDatepickerChanges}
        />

      </Layout>

      <Layout style={styles.rowContainer}>

        <Datepicker
          style={styles.picker}
          status='info'
          placeholder='Info'
          {...infoDatepickerChanges}
        />

        <Datepicker
          style={styles.picker}
          status='warning'
          placeholder='Warning'
          {...warningDatepickerChanges}
        />

      </Layout>

      <Layout style={styles.rowContainer}>

        <Datepicker
          style={styles.picker}
          status='danger'
          placeholder='Danger'
          {...dangerDatepickerChanges}
        />

        <Datepicker
          style={styles.picker}
          status='basic'
          placeholder='Basic'
          {...basicDatepickerChanges}
        />

      </Layout>

    </Layout>
  );
};

const styles = StyleSheet.create({
  container: {
    minHeight: 520,
  },
  rowContainer: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  picker: {
    flex: 1,
    margin: 8,
  },
});
