import React from 'react';
import { StyleSheet, View } from 'react-native';
import {
  Button,
  Icon,
  IndexPath,
  Layout,
  Popover,
  Select,
  SelectGroup,
  SelectItem,
  Text,
} from '@ui-kitten/components';

const placements = {
  top: [
    'top',
    'top start',
    'top end',
  ],
  bottom: [
    'bottom',
    'bottom start',
    'bottom end',
  ],
  left: [
    'left',
    'left start',
    'left end',
  ],
  right: [
    'right',
    'right start',
    'right end',
  ],
};

const findPlacementForIndexPath = (index) => {
  const group = Object.keys(placements)[index.row];
  return placements[group][index.section];
};

const HeartIcon = (props) => (
  <Icon {...props} name='heart'/>
);

export const PopoverPlacementShowcase = () => {

  // Updating `selectedIndex` if marking items selected is not needed.
  const [visible, setVisible] = React.useState(false);
  const [placementIndex, setPlacementIndex] = React.useState(new IndexPath(1, 0));
  const placement = findPlacementForIndexPath(placementIndex);

  const toggleMenu = () => {
    setVisible(!visible);
  };

  const onPlacementSelect = (index) => {
    setPlacementIndex(index);
  };

  const togglePopover = () => {
    setVisible(!visible);
  };

  const renderToggleButton = () => (
    <Button onPress={toggleMenu}>
      TOGGLE POPOVER
    </Button>
  );

  const onPlacementItemPress = ({ index }) => {
    setPlacementIndex(index);
  };

  const renderPlacementItem = (title) => (
    <SelectItem title={title} onPress={onPlacementItemPress}/>
  );

  const renderPlacementGroup = (title) => (
    <SelectGroup title={title}>
      {placements[title].map(renderPlacementItem)}
    </SelectGroup>
  );

  return (
    <Layout>

      <Select
        placeholder='Select Placement'
        value={placement}
        selectedIndex={placementIndex}
        onSelect={onPlacementSelect}>
        {Object.keys(placements).map(renderPlacementGroup)}
      </Select>

      <View style={styles.buttonContainer}>
        <Popover
          anchor={renderToggleButton}
          visible={visible}
          placement={placement}
          onBackdropPress={togglePopover}>
          <Layout style={styles.popoverContent}>
            <Text style={styles.text} category='h1'>
              Welcome to UI Kitten 
            </Text>
            <Text style={styles.text} category='s1'>
              Start with editing App.js to configure your App
            </Text>
            <Text style={styles.text} appearance='hint'>
              For example, try changing theme to Dark by simply changing an import
            </Text>
            <Button style={styles.likeButton} accessoryLeft={HeartIcon}>
              LIKE
            </Button>
          </Layout>
        </Popover>
      </View>

    </Layout>
  );
};

const styles = StyleSheet.create({
  buttonContainer: {
    justifyContent: 'center',
    alignItems: 'center',
    height: 376,
  },
  button: {
    width: 192,
  },
  popoverContent: {
    justifyContent: 'center',
    alignItems: 'center',
    padding: 24,
  },
  text: {
    textAlign: 'center',
  },
  likeButton: {
    marginVertical: 16,
  },
});
